<template>
  <div v-if="visible" id="MiniPlayer" class="miniplayer">
    <div class="miniplayer__controls">
      <button
        v-if="!player.playing"
        @click="play()"
        class="miniplayer__control miniplayer__control--play"
        aria-label="Play Song"
        :disabled="disabled"
      >
        <i class="fas fa-play"></i>
      </button>

      <button
        v-if="player.playing"
        @click="pause()"
        class="miniplayer__control miniplayer__control--pause"
        aria-label="Pause Song"
        :disabled="disabled"
      >
        <i class="fas fa-pause"></i>
      </button>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
  name: "MiniPlayer",
  data() {
    return {
      visible: false
    };
  },
  computed: {
    ...mapState(["current_song", "player"]),

    disabled() {
      return this.current_song.id === null || this.player.processing;
    },
    show() {
      if (this.visible) return true;
      if (this.$route.name === "Home" && this.player.playing) return true;
      return this.player.playing;
    }
  },
  watch: {
    show() {
      if (this.show === true) this.visible = true;
    }
  },
  methods: {
    ...mapActions(["play", "pause"])
  }
};
</script>
