<template>
  <div id="app">
    <SeoHead />
    <router-view />
    <MiniPlayer />
  </div>
</template>

<script>
import { mapMutations, mapActions } from "vuex";
import MiniPlayer from "@/components/elements/MiniPlayer";
import SeoHead from "@/components/elements/SeoHead";
import scPlayer from "@/utilities/soundcloud";

export default {
  name: "App",
  components: { MiniPlayer, SeoHead },
  mounted() {
    // Update the progress bar
    scPlayer.on("timeupdate", () => {
      this.setPlayhead(scPlayer.audio.currentTime * 1000);
    });

    // Song Events
    scPlayer.on("play", () => this.play());
    scPlayer.on("pause", () => this.pause());
    scPlayer.on("ended", () => this.next());
  },
  methods: {
    ...mapMutations(["setPlayhead"]),
    ...mapActions(["play", "pause", "next"])
  }
};
</script>

<style lang="scss">
@import "src/scss/app.scss";
</style>
