import Vue from "vue";
import Vuex from "vuex";
import scPlayer from "@/utilities/soundcloud";
import config from "@/config.js";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    api_available: {},
    current_song: {
      duration: 0,
      id: null
    },
    menu: [
      {
        name: "Profile",
        value: "#profile",
        current: true,
        emphasize: false
      },
      {
        name: "Experience",
        value: "#experience",
        current: false,
        emphasize: false
      },
      {
        name: "Skills",
        value: "#skills",
        current: false,
        emphasize: false
      },
      {
        name: "Portfolio",
        value: "#portfolio",
        current: false,
        emphasize: false
      },
      {
        name: "Contact",
        value: "#contact",
        current: false,
        emphasize: true
      }
    ],
    player: {
      playhead: 0,
      playing: false,
      prevSongThreshold: 3,
      processing: false,
      volume: 10
    },
    playlist_current: {
      index: -1,
      playlist: {},
      tracks: [],
      uri: ""
    },
    playlists: {},
    seo: {
      title: config.seo.title,
      description: config.seo.description
    }
  },
  getters: {
    artwork(state) {
      return state.current_song.artwork_url !== undefined
        ? state.current_song.artwork_url
        : null;
    },
    firstSongIndex() {
      return 0;
    },
    lastSongIndex(state) {
      if (state.playlist_current.index === -1) return -1;
      return state.playlist_current.playlist.track_count - 1;
    },
    nextSongIndex(state) {
      if (state.playlist_current.index === -1) return -1;
      return state.current_song.playlist_index + 1;
    },
    prevSongIndex(state) {
      if (state.playlist_current.index === -1) return -1;
      return state.current_song.playlist_index - 1;
    },
    streamUrl(state) {
      if (state.current_song.id === null) return "";
      return `https://api.soundcloud.com/tracks/${state.current_song.id}/stream`;
    },
    timeCurrent(state) {
      return state.player.playhead / 1000;
    },
    timeCountdown(state, getters) {
      return getters.timeDuration - getters.timeCurrent;
    },
    timeDuration(state) {
      return state.current_song.duration / 1000;
    }
  },
  mutations: {
    setApiAvailability(state, payload) {
      Vue.set(state.api_available, payload.uri, payload.status);
    },
    // Payload: { index, uri }
    setCurrentSong(state, payload) {
      // Set Current Playlist
      state.playlist_current = {
        index: payload.index,
        playlist: state.playlists[payload.uri],
        tracks: state.playlists[payload.uri].tracks,
        uri: payload.uri
      };

      // Set Current Song
      state.current_song = Object.assign(
        state.playlists[payload.uri].tracks[payload.index],
        {
          playlist_uri: payload.uri,
          playlist_index: payload.index
        }
      );
    },
    setPlayhead(state, time) {
      state.player.playhead = time;
    },
    setPlaying(state, status) {
      state.player.playing = status;
    },
    setPlaylist(state, payload) {
      Vue.set(state.playlists, payload.uri, payload.playlist);
      Vue.set(state.api_available, payload.uri, true);
    },
    setProcessing(state, boolean) {
      state.player.processing = boolean;
    },
    setSiteTitle(state, title) {
      state.seo.title = title;
    }
  },
  actions: {
    next({ dispatch, commit, state, getters }) {
      if (state.current_song.playlist_index === getters.lastSongIndex)
        commit("setCurrentSong", {
          index: getters.firstSongIndex,
          uri: state.current_song.playlist_uri
        });
      else
        commit("setCurrentSong", {
          index: getters.nextSongIndex,
          uri: state.current_song.playlist_uri
        });

      dispatch("play");
    },
    prev({ dispatch, commit, state, getters }) {
      // If less than threshold && first song, go to last song
      if (
        getters.timeCurrent < state.player.prevSongThreshold &&
        state.current_song.playlist_index === getters.firstSongIndex
      )
        commit("setCurrentSong", {
          index: getters.lastSongIndex,
          uri: state.current_song.playlist_uri
        });
      // If less than threshold, go back a song
      else if (getters.timeCurrent < state.player.prevSongThreshold)
        commit("setCurrentSong", {
          index: getters.prevSongIndex,
          uri: state.current_song.playlist_uri
        });
      // Otherwise stop song
      else dispatch("stop");

      // Play song after conditional complete
      dispatch("play");
    },
    pause({ commit }) {
      scPlayer.pause({ streamUrl: this.streamUrl });
      commit("setSiteTitle", config.seo.title);
      commit("setPlaying", false);
    },
    play({ commit, state, getters }) {
      commit("setProcessing", true);
      scPlayer.play({ streamUrl: getters.streamUrl }).then(() => {
        commit("setPlaying", true);
        commit(
          "setSiteTitle",
          `"${state.current_song.title}" - ${config.seo.title}`
        );
        commit("setProcessing", false);
      });
    },
    stop({ commit, getters }) {
      scPlayer.stop({ streamUrl: getters.streamUrl });
      commit("setSiteTitle", config.seo.title);
      commit("setPlaying", false);
    }
  }
});
